(function ($) {
  // Wait for the block editor to initialize
  wp.domReady(function () {
    // Get the current block editor instance
    var editor = wp.data.select('core/block-editor');

    // Function to toggle the visibility of the ACF field
    function toggleACFFieldVisibility() {
      var selectedBlockClientId = editor.getSelectedBlockClientId();
      var selectedBlock = editor.getBlock(selectedBlockClientId);
      var blockClass = 'is-style-primary'; // Replace with your desired block class

      if (selectedBlock.classList && selectedBlock.classList.contains(blockClass)) {
        // Hide the ACF field with the ID 'button_one'
        acf.getField('button_group_link_two').hide();
      } else {
        // Show the ACF field with the ID 'button_one'
        acf.getField('button_group_link_two').show();
      }
    }

    // Subscribe to block changes
    editor.subscribe(function () {
      toggleACFFieldVisibility();
    });

    // Initialize the ACF field visibility on page load
    toggleACFFieldVisibility();
  });
})(jQuery);
